
@import 'styles/common-variables.scss';

@mixin fixed-full ($zIndex: 1) {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: $zIndex;
}

.#{$class-prefix} {
    &-modal {
        @include fixed-full($modal-z-index);
        background-color: rgba(0, 0, 0, 0.35);
        outline: 0;
    }

    &-modal-wrap {
        max-width: 60%;
        margin: 0 auto;
        margin-top: 3.5rem;
        background: #fff;
        outline: 0;
        border-radius: 6px;
        &:focus {
            border: 2px solid rgb(78, 78, 204);
        }
    }

    &-modal-title {
        border-bottom: 1px solid gray;
        padding: 0.35rem;
    }

    &-modal-footer {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        border-top: 1px solid rgba(100, 100, 100, 0.3);
        padding: 0.35rem;
    }
}