.app {
    position: relative;
    margin: 0 auto;
    margin: auto;
    min-width: 375px;
    min-height: 400px;
}

.app-notify-install {
  position: absolute;
  top: 20px;
  right: 10px;
  border: 1px solid gray;
  border-radius: 10px;
  padding: 8px 10px;
  .app-notify-install-close {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    margin-left: 14px;
    border-radius: 100%;
    background: wheat;
    color: blue;
    cursor: pointer;
  }
}
.btn {
    padding: 0.375rem 0.75rem;
    border: 1px solid #e7f1fd;
    border-radius: 0.35rem;
    color: #fff;
}

.btn-block {
    display: flex;
    align-items: center;
    justify-content: center;
}

/********************************           home */
@media screen and (min-width: 768px) {
    .app {
        width: 550px;
        height: auto;
        border: solid 1px rgba(200, 200, 200, 0.35);
        border-radius: 10px;
        margin-top: 10vh;
        // box-shadow: rgb(189, 186, 186) 0px 0px 20px;
    }
}